xt-light\"> {{ job }} </p>\n                  <b-button variant=\"primary\">\n                    Request Time Off\n                  </b-button>\n                </b-col>\n              </b-row>\n            </b-container>\n            <b-container fluid class=\"p-4\">\n            <p><h2> My contract:</h2></p>\n                <p>  Started at {{ beginDate }} </p>\n                <p v-if=\"endDate\">  Finish at {{ endDate }} </p>\n                <p>  {{ hoursAWeek}} hours per week </p>\n            </b-container>\n          </b-col>\n          <b-col cols=\"12\" lg=\"8\">\n            <b-alert variant=\"success\" show dismissible>Welcome back {{name}}!</b-alert>\n            <p>Your infos:</p>\n          </b-col>\n        </b-row>\n        </b-container>\n      </b-tab>\n      <b-tab title=\"Updating my informations\">\n        <b-container fluid class=\"p-4\">\n            <label> Name </label>\n            <b-form-input v-model=\"form.name_form\"></b-form-input>\n            <label>Birthdate</label>\n            <b-form-input type=\"date\"></b-form-input>\n            <label>Adress</label>,\n            <b-form-input></b-form-input>\n            <label>Phone number</label>\n            <b-form-input type=\"text\"></b-form-input>\n            <label> Email </label>\n            <b-form-input type=\"email\"></b-form-input>\n            <p>Your infos : {{ form.name_form }}</p>\n        </b-container>\n      </b-tab>\n      <b-tab title=\"Planning\">\n        <calendar v-model=\"events\"></calendar>\n      </b-tab>\n    </b-tabs>\n  </div>\n</template>\n<script>\n\nimport Calendar from '@/components/Calendar';\n\nfunction formatDate(date) {\n  const monthNames = [\n    'January', 'February', 'March',\n    'April', 'May', 'June', 'July',\n    'August', 'September', 'October',\n    'November', 'December',\n  ];\n\n  const day = date.getDate();\n  const monthIndex = date.getMonth();\n  const year = date.getFullYear();\n\n  return `${day} ${monthNames[monthIndex]} ${year}`;\n}\n\nexport default {\n  name: 'Employee',\n  data() {\n    return {\n      app_name: 'GTA-Ynov-Vue',\n      events: [],\n      name: 'Basic Name',\n      job: 'This is a job',\n      beginDate: formatDate(new Date()),\n      endDate: formatDate(new Date()),\n      hoursAWeek: 'Too Much',\n      event: [],\n      form: {\n        name_form: this.name,\n        job_form: this.job,\n      },\n    };\n  },\n  components: {\n    Calendar\n  },\n  watch: {\n    form: {\n      handler(val, oldval) {\n        this.name = val.name_form;\n        console.log(this.name);\n      },\n      deep: true,\n    },\n  },\n};\n\n</script>\n\n<!-- Add \"scoped\" attribute to limit CSS to this component only -->\n<style scoped>\n  h1,\n  h2 {\n    font-weight: normal;\n  }\n\n  ul {\n    list-style-type: none;\n    padding: 0;\n  }\n\n  li {\n    display: inline-block;\n    margin: 0 10px;\n  }\n\n  a {\n    color: #42b983;\n  }\n</style>\n"],"sourceRoot":""}]);

